<template>
  <div class="login">
        <!-- <form class="login-form" v-on:submit="loginnew"> -->
        <form class="login-form" @submit.prevent="loginnew()">
          <el-input type="email" required v-model="input" name="email" placeholder="Email"></el-input>
          <el-input type="password" required v-model="input1" name="password" placeholder="Password"></el-input>
          <el-input type="submit" value="loginnew"></el-input>
        </form>

    <!-- <pre>{{ $data }}</pre> -->
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import router from "../router";

export default {
  data: () => ({
      input: "",
      input1: "",
      errorlogin: false,
  }),
  methods: {
    loginnew () {
      let data = {
          email: this.input,
          password: this.input1
        }
        axios.post("/api/login", data)
          .then((response) => {
            console.log("Logged in")
            router.push("/dashboard")
          })
          .catch((errors) => {
            console.log(errors);
            this.errorlogin = true;
            this.open6()
            console.log("Cannot login");
          })
    },
    open6() {
        this.$notify.error({
          title: 'Error',
          message: 'Username or password is incorrect'
        });
      },
  },
  created () {
  }
}
</script>

<style scoped lang="scss">
.login {
  border: 1px solid black;
  border-radius: 5px;
  padding: 1.5rem;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  .container-loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    img {
      width: 2rem;
      height: 2rem;
    }
  }
  form {
    display: flex;
    flex-flow: column;
    *:not(:last-child) {
      margin-bottom: 1rem;
    }
    input {
      padding: 0.5rem;
    }
    button {
      padding: .5rem;
      background-color: lightgray;
      border: 1px solid gray;
      border-radius: 3px;
      cursor: pointer;
      &:hover {
        background-color: lightslategray;
      }
    }
  }
}
</style>



