<template>
  <div>
    <h2>Dashboard</h2>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import router from "../router";

export default {
  data: () => ({

      user: {}

  }),
  methods: {
    getUserData: function() {
      // let self = this
      axios
        .get("/api/user")
        .then(response => {
          console.log(response.data.user);
          // alert(response)
          this.user = response.data.user;
          // self.$set(this, "user", response.data.user)
        })
        .catch(errors => {
          console.log(errors);
          // alert(errors)
          router.push("/");
        });
    },
  },
  mounted() {
    // this.getUserData()

  },
  created() {

  }
};
</script>



