
<template>
  <div id="app">
    <router-view name="header"></router-view>
    <main>
      <!-- <transition origin="center" mode="out-in" :duration="250"> -->
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </main>
    <router-view name="footer"></router-view>

    <!-- <router-view/> -->
    <!-- <pre>{{ $data }}</pre> -->
  </div>
</template>

<script>
/* eslint-disable */
import router from "./router";
import axios from "axios";
export default {
  name: "app",
  data() {
    return {
    };
  },
  created() {

  },
  mounted() {

  },
  methods: {
    status: function() {
      axios
        .get("/api/status")
        .then(response => {
          console.log(response);
          // this.authenticated = response.data;
        })
        .catch(errors => {
          console.log(errors);
          // this.authenticated = false;
        });
    },
    logout: function(e) {
      alert("logout");
      axios.get("/api/logout").then(() => {
        router.push("/");
      });
    },
  },
  created() {
    // console.log(this.links);
  }
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



